import React from "react";

interface IButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  size: "small" | "large";
  variant: "dark" | "light";
  onClick?: () => any;
  border?: boolean;
  className?: string;
}

const Button = (props: React.PropsWithChildren<IButtonProps>) => {
  const { size, variant, border, onClick, className, children } = props;

  const extraClasses = `${size === "small" ? "text-sm h-10" : "text-lg h-12"} ${
    variant === "dark"
      ? "bg-main-100 text-secondary-100 border-secondary-100 hover:bg-main-70 hover:outline-main-100"
      : "bg-secondary-100 text-main-100 border-main-100 hover:bg-secondary-30 hover:outline-secondary-100"
  } ${border ? "border border-solid" : ""}`;

  return (
    <button
      onClick={onClick}
      className={`h-10 min-w-10 m-2 justify-center px-3 ${extraClasses} ${className}`}
    >
      {children}
    </button>
  );
};

export default Button;
