import React from "react";
import { Link } from "gatsby";

import ContentContainer from "./contentContainer";
import ContactsLinks from "./contactsLinks";

const Footer = () => {
  const links = ["Services", "Projects", "Blog", "About Us"];

  return (
    <footer className="bg-main-100 py-4">
      <ContentContainer className="min-h-[9rem]">
        <nav className="w-full flex items-center">
          {/* Logo */}
          <Link
            to="/"
            className="flex flex-1 items-center justify-center basis-1/3"
          >
            <h1 className="text-3xl sm:text-4xl md:text-5xl font-medium cursor-pointer text-secondary-100">
              CodeCave
            </h1>
          </Link>

          {/* Main links */}
          <ul className="flex justify-center flex-wrap shrink basis-1/3">
            {links.map((link, i) => (
              <li key={`mainLinks-${i}`} className="p-2 w-max">
                <Link
                  to={`/${link.toLowerCase().split(" ").join("-")}`}
                  className="text-xl text-secondary-30 underline"
                >
                  {link}
                </Link>
              </li>
            ))}
          </ul>

          {/* Contacts menu */}
          <div className="flex flex-wrap shrink basis-1/3 justify-center">
            <ContactsLinks />
          </div>
        </nav>
      </ContentContainer>
    </footer>
  );
};

export default Footer;
