import React, { useState } from "react";
import { Link } from "gatsby";

import { AiOutlineMenu } from "@react-icons/all-files/ai/AiOutlineMenu";
import { AiOutlineClose } from "@react-icons/all-files/ai/AiOutlineClose";

import Button from "./button";
import ContentContainer from "./contentContainer";
import ContactsLinks from "./contactsLinks";

const Navbar = () => {
  const [isBurgerOpen, setIsBurgerOpen] = useState<boolean>(false);
  const [isContactsOpen, setIsContactsOpen] = useState<boolean>(false);

  const links = ["Services", "Projects", "Blog", "About Us"];

  return (
    <nav className="sticky top-0 z-20 bg-secondary-100">
      <ContentContainer>
        <div className="w-full">
          <div className="mx-auto max-w-7xl">
            <div className="relative flex h-65px items-center justify-between">
              {/* burger buttons */}
              <div className="absolute inset-y-0 left-0 flex items-center sm:hidden">
                <button
                  type="button"
                  className="inline-flex items-center justify-center text-main-70 hover:bg-secondary-30"
                  aria-controls="mobile-menu"
                  aria-expanded="false"
                  onClick={() => setIsBurgerOpen(!isBurgerOpen)}
                >
                  <AiOutlineMenu
                    className={`text-2xl ${isBurgerOpen ? "hidden" : ""}`}
                  />
                  <AiOutlineClose
                    className={`text-2xl ${isBurgerOpen ? "" : "hidden"}`}
                  />
                </button>
              </div>
              {/* main links */}
              <div className="flex flex-1 items-center justify-center sm:justify-between">
                <Link to="/">
                  <h1 className="text-3xl sm:text-4xl font-medium cursor-pointer">
                    CodeCave
                  </h1>
                </Link>
                <ul className="hidden sm:flex flex-1 justify-center">
                  {links.map((link, i) => (
                    <li key={`mainLinks-${i}`} className="mr-4">
                      <Link
                        to={`/${link.toLowerCase().split(" ").join("-")}`}
                        className="text-xl text-main-70 underline"
                      >
                        {link}
                      </Link>
                    </li>
                  ))}
                </ul>
              </div>
              {/* Contacts menu */}
              <div className="relative">
                <Button
                  id="user-menu-button"
                  variant="dark"
                  size="small"
                  aria-expanded="false"
                  aria-haspopup="true"
                  onClick={() => setIsContactsOpen(!isContactsOpen)}
                >
                  <span className="text-xl">Contact us</span>
                </Button>
                <div
                  className={`absolute flex flex-col align-center top-full left-0 z-10 mx-2 bg-main-70/20 py-1 shadow-lg ${
                    isContactsOpen ? "" : "hidden"
                  }`}
                  role="menu"
                  aria-orientation="vertical"
                  aria-labelledby="user-menu-button"
                  tabIndex={-1}
                >
                  <ContactsLinks />
                </div>
              </div>
            </div>
          </div>
          <div
            className={`sm:hidden ${!isBurgerOpen ? "hidden" : ""}`}
            id="mobile-menu"
          >
            <ul className="space-y-1 px-2 pb-3 pt-2">
              {links.map((link, i) => (
                <li key={`mainLinks-${i}`} className="mr-4">
                  <Link
                    to={`/${link.toLowerCase().split(" ").join("-")}`}
                    className="text-lg text-main-70 underline"
                  >
                    {link}
                  </Link>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </ContentContainer>
    </nav>
  );
};

export default Navbar;
